
.cell{
  height: 100%;
  width: 100%;
  border: none;
  display: block;
  -webkit-transition: all .1s;
  transition: all .1s;
}
.cell:hover, .cell:focus{
  background: #eee;
  -webkit-transform: scale(1.1) translateY(-4px);
          transform: scale(1.1) translateY(-4px);
  opacity: .9;
}
.cell.is-alive{
  background: #fff;
  -webkit-transform: scale(1.3) translateY(-10px);
          transform: scale(1.3) translateY(-10px);
  opacity: .9;
}



