
*{
  margin: 0;
  padding: 0;
}
html, body, #app{
  height: 100%;
}
.controls{
  position: absolute;
  bottom: 0;
  width: 100%;
  height: 60px;
  background: #333;
  text-align: center;
}
.controls button{
  font-size: 42px;
  margin: 8px 4px 0 0;
  background: none;
  border: none;
  color: #aaa;
}
.controls button.active{
  color: #fff;
}



